.new-publication__filters-wrapper {
  display: flex;
  position: relative;
  height: 72px;
}
@media (min-width: 960px) {
  .new-publication__filters-wrapper {
    align-items: center;
  }
}
.new-publication__range-wrapper::after {
  content: "";
  position: absolute;
  left: -40px;
  bottom: 0;
  width: calc(100% + 40px*2);
  height: 1px;
  background-color: #dddfe0;
}
@media (min-width: 660px) {
  .new-publication__range-wrapper::after {
    display: none;
  }
}
@media (min-width: 960px) {
  .new-publication__filters-wrapper::after {
    content: "";
    position: absolute;
    left: -40px;
    bottom: 0;
    width: calc(100% + 40px*2);
    height: 1px;
    background-color: #dddfe0;
  }
}
